* {
  box-sizing: border-box;
}

.postcard {
  max-width: 350px;
  margin: 50px auto 0;
  padding: 20px;
  background: #fffdee;
  border: 10px solid transparent;
  border-image: 10
    repeating-linear-gradient(
      135deg,
      #c42061,
      #c42061 10px,
      transparent 10px,
      transparent 20px,
      #fbcfdf 20px,
      #fbcfdf 30px,
      transparent 30px,
      transparent 40px
    );
  font-family: "Playfair Display";
  color: #605756;
}
.formrow {
  margin-bottom: 15px;
}
.formrow input[type="text"],
.formrow input[type="email"],
.formrow textarea {
  width: 100%;
  padding: 0 10px;
  line-height: 35px;
  border-width: 0;
  outline: none;
  background: transparent
    url(https://html5book.ru/wp-content/uploads/2016/12/bg-form.png) bottom left
    repeat-x;
  background-size: 8px 35px;
  font-family: "Playfair Display";
}
.formrow textarea {
  background-attachment: local;
  background-repeat: repeat;
  resize: none;
}
.formrow input[type="submit"] {
  padding: 10px 20px;
  margin-top: 10px;
  background: #f67ca5;
  border-width: 0;
  cursor: pointer;
  outline: none;
  font-family: "Playfair Display";
  font-size: 13px;
  letter-spacing: 1px;
  color: #fffdee;
  text-transform: uppercase;
  transition: 1s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.formrow input[type="submit"]:hover {
  background: #c42061;
}

.floatingButton {
  text-decoration: none;
  display: inline-block;
  min-width: 140px;
  height: 45px;
  line-height: 45px;
  border-radius: 45px;
  margin: 10px 20px;
  font-family: "Montserrat", sans-serif;
  font-size: 11px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 3px;
  font-weight: 600;
  color: #524f4e;
  background: white;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
}
.floatingButton:hover {
  background: #2ee59d;
  box-shadow: 0 15px 20px rgba(46, 229, 157, 0.4);
  color: white;
  transform: translateY(-7px);
}
.floatingButton {
  margin-left: 50px;
}

.edit {
  border: 1px solid #9e9e9e;
  margin-left: 15px;
  color: #000000;
  padding: 3px;
  margin-top: 2px;
  margin-bottom: 2px;
  font-size: 11px;
  font-family: Verdana;
  background: #fff;
}
.centr {
  text-align: center;
  color: white;
}
